.goback_container {
  width: 1128px;
  margin: auto;
  margin-top: 19px;
  background-color: var(--tab-active-bg-color);
  border-radius: 6px 6px 0px 0px;
  text-decoration: none;
}

.goback_container_text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  padding: 16px 23px;
  color: var(--tab-active-color);
}
.goback_container_text_go {
  margin: 0;
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-18);
  line-height: 22px;
}
.student_from_container {
  width: 1128px;
  margin: auto;
  margin-top: 19px;
  background: #ffffff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
}
.staff_header_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 18px 11px 18px;
}
.staff_header_text {
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-14);
  line-height: var(--line-height-17);
  color: #121212;
  cursor: pointer;
  margin: 0;
}
.staff_profile_photo_container {
  padding: 20px 18px 11px 18px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.staff_profile_photo_container_inner {
  position: relative;
  display: flex;
  align-items: center;
  gap: 12px;
}
.staff_profile_photo_image {
  width: 100px;
  height: 100px;
  border-radius: 12px;
}
.staff_profile_photo_info {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  gap: 8px;
}
.staff_profile_photo_name {
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-16);
  line-height: var(--line-height-17);
  color: #121212;
  margin: 0;
}
.staff_profile_photo_index {
  font-weight: var(--font-weight-400);
  font-size: var(--font-size-14);
  line-height: var(--line-height-17);
  color: rgba(18, 18, 18, 0.8);
  margin-bottom: 0.2rem;
}
.card_hold_container {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}
.card_hold_container > img {
  width: 24px;
  height: 16px;
}
.card_hold_container > h6 {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #436dfa;
  margin: 0;
}
.sataf_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 2rem;
}
.sataf_container_count {
  border-radius: 6px;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  background: #fff;
  box-shadow: 0px 4px 4px 0px #0057ff;
  padding: 0.3rem 0.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
.sataf_container_count_inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.sataf_container_count_inner > h6,
.satafcci_text > h6,
.satafcci_amount > h6 {
  color: #121212;
  font-size: 14px;
  font-weight: 500;
  margin: 0;
}
.sataf_container_count_inner > h6 {
  margin-bottom: 0.4rem;
}
.satafcci_amount {
  margin-bottom: 0.2rem;
}
.satafcci_amount > h6 {
  text-align: center;
  border: 0.4px solid rgba(0, 0, 0, 0.5);
  height: 2rem;
  width: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.satafcci_text {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.8rem;
}
.satafcci_text > img {
  width: auto;
  height: 1.3rem;
}
